$white-color:  var(--white-color);
$black-color: #000000; */


:root {
    /* color */
    --main-color : #0eae44;
    --white-color : #eeeeee;
    --black-color: #000000;  
  }


/* ðŸ¦„navbar */
#navbar {
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0.1 rem;
    background-color: transparent;
    background-color: var(--main-color);
    z-index: 1;
}
#navbar .navbar_menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#navbar .navbar_menu_item {
    padding: 8px 12px;
    margin: 0 4px;
    cursor: pointer;
    border: 1px solid transparent;
    border-radius: 0.5rem;
    transition: all 0.3s;
}
#navbar .navbar_menu_item:hover {
    border-color:  var(--white-color);
    transform: scale(1.1);
}
#navbar a, #navbar .navbar_icon {
    color:  var(--white-color);
}
/* ðŸ¦„about me */
#aboutme {
    padding: 6rem 3rem 3rem 3rem;
}
#aboutme .avatar {
    width: 30%;
    border-radius: 0.5rem;
}
#language {
    padding: 3rem;
}
/* ðŸ¦„skills */
#skills {
    padding: 3rem;
}
.about_skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;
}
.about_skills .skill_icon {
    width: 80px;
    height: 80px;
    line-height: 80px;
    border: 1px solid  var(--main-color);
    border-radius: 50%;
    color:  var(--main-color);
    font-size: 40px;
    margin: auto;
    margin-bottom: 16px;
    transition: all 0.3s;
}
.about_skills .skill_icon:hover {
    font-weight: bold;
    transform: rotate(-30deg) scale(1.2);
}
.about_skills p {
    font-weight: bold;
    font-size: 1rem;
}
/* ðŸ¦„projects */
#projects {
    padding: 3rem;
}
#projects .project_container_line {
    padding: 3rem 0 0 0;
}
#projects img {
    width: 15%;
}
#projects .project_item {
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: center;
    padding: 1rem;
    border-radius: 0.5rem;
    transition: all 0.3s;
}
#projects .project_item img {
    box-shadow: 0.2rem 0.2rem 0.2rem  var(--white-color);
}
#projects .project_item:hover {
    transform: scale(1.05);
    background-color:  var(--white-color);
}
#projects .project_item img {
    margin: auto 5rem;
}

.project_name {
    color: var(--main-color);
    padding-top: 1rem;
}

/* ðŸ¦„contact */
#contact {
    padding: 3rem;
}
#contact .icon {
    width: 80px;
    height: 80px;
    color:  var(--main-color);
    font-size: 60px;
    transition: all 0.3s;
}
#contact .icon:hover {
    transform: scale(1.2);
}
/* @ 576px */
@media (max-width: 576px) {
    html {
        font-size: 75%;
   }
    #skills .skill_icon {
        width: 40px;
        height: 40px;
        line-height: 40px;
        font-size: 25px;
   }
    .project_description {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
   }
    #contact .icon {
        font-size: 40px;
   }
}
@media (min-width: 1200px) {
    html {
        font-size: 130%;
   }
    body {
        font-size: 130%;
   }
    #skills, #language {
        max-width: 1200px;
        margin: auto;
   }
}
